'use client';

import ProtectedRoute from '@/components/ProtectedRoute';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Image from 'next/image';

export default function DashboardLayout({ children }: { children: React.ReactNode }) {
  const { user, userData, logout } = useAuth();
  const router = useRouter();

  const handleLogout = async () => {
    await logout();
    router.push('/admin/login');
  };

  return (
    <ProtectedRoute>
      <div className="min-h-screen bg-gray-100">
        {/* Header */}
        <header className="bg-white shadow">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex justify-between items-center">
            <div className="flex items-center gap-4">
              <Image src="/logo.png" alt="CBZ Bank" width={50} height={50} />
              <h1 className="text-2xl font-bold text-gray-800">
                Admin Dashboard
              </h1>
            </div>
            <div className="flex items-center gap-4">
              <div className="text-right">
                <p className="text-sm text-gray-600">{user?.email}</p>
                <p className="text-xs text-gray-500 capitalize">{userData?.role}</p>
              </div>
              <button
                onClick={handleLogout}
                className="bg-cbz-red text-white px-4 py-2 rounded hover:bg-red-700 transition"
              >
                Logout
              </button>
            </div>
          </div>
        </header>

        {/* Navigation */}
        <nav className="bg-white border-b border-gray-200">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex space-x-8">
              <Link
                href="/admin/dashboard"
                className="border-b-2 border-transparent hover:border-cbz-red px-1 py-4 text-sm font-medium text-gray-700 hover:text-cbz-red transition"
              >
                Overview
              </Link>
              <Link
                href="/admin/dashboard/rates"
                className="border-b-2 border-transparent hover:border-cbz-red px-1 py-4 text-sm font-medium text-gray-700 hover:text-cbz-red transition"
              >
                Manage Rates
              </Link>
              <Link
                href="/admin/dashboard/media"
                className="border-b-2 border-transparent hover:border-cbz-red px-1 py-4 text-sm font-medium text-gray-700 hover:text-cbz-red transition"
              >
                Media
              </Link>
              {userData?.role === 'admin' && (
                <Link
                  href="/admin/dashboard/users"
                  className="border-b-2 border-transparent hover:border-cbz-red px-1 py-4 text-sm font-medium text-gray-700 hover:text-cbz-red transition"
                >
                  Users
                </Link>
              )}
            </div>
          </div>
        </nav>

        {/* Main Content */}
        <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          {children}
        </main>
      </div>
    </ProtectedRoute>
  );
}
